<template>
    <div id="app">
        <demo>
            <p slot="bodys"></p>
            <p slot="header" @click="console"></p>
        </demo>
        <el-button></el-button>
    </div>
</template>

<script>
import demo from './components/demo'
export default {
    components:{
        demo
    },
    data(){
        return {
            num:'1'
        }
    },
    methods:{
        console(){
            console.log(this.$slots.bodys)
            console.log(this.num)
        }
    }
}
</script>


