<template>
    <div>

        <!-- input -->
        <template v-if="show">
            <label></label>
            <input type="text" placeholder="">
        </template>
        <template v-else>
            <label></label>
            <input type="text" placeholder="">
        </template>

          <!-- 
                1.v-model
                2.key
           -->

        <!-- <template v-if="show">
            <label></label>
            <input type="text" placeholder="" v-model="name">
        </template>
        <template v-else>
            <label></label>
            <input type="text" placeholder="" v-model="copy">
        </template> -->


        <!-- key -->

         <!-- <template v-if="show">
            <label></label>
            <input type="text" placeholder="" key="name-value">
        </template>
        <template v-else>
            <label></label>
            <input type="text" placeholder="" v-model="copy" key="copy-value">
        </template> -->
        <el-button @click="show = !show"></el-button>
    </div>
</template>
<script>
export default {
    data(){
        return {
            show:true,
            name:'',
            copy:''
        }
    },
    methods:{

    }
}
</script>


