<template>
    <div>
        <h2></h2>

        <h3 v-for="(item,index) in intro" :key="index">{{index  + 1}} . {{item.name}}</h3>
        <p>{{total}}</p>
        <p>{{parentData}}</p>
        <child-talk :transMsg="msg" @changeNum="add"></child-talk>
        <child-talks>
            <h3 slot="header"></h3>
        </child-talks>
    </div>
</template>
<script>
import childTalk from './childTalk'
import childTalks from './childTalk1'
export default {
    components:{
        childTalk,
        childTalks
    },
    data(){
        return {
            msg:'',
            total:0,
            parentData:'',
            intro:[
                {
                    name:',,'
                },
                {
                    name:' props$emit'
                },
                {
                    name:' v-model'
                },
                {
                    name:'/bus'
                },
                {
                    name:' $parent,$children,$refs'
                },
                {
                    name:'1. props  2. event  3. slot'
                },
                {
                    name:','
                },
            ]
        }
    },
    methods:{
        add(){
            this.total += 1
        }
    }
}
</script>
<style>
    h2 {
        text-align: center;
    }
    h3 {
        text-align: center;
        color:dimgrey;
        font-size:18px;
        margin-top: 40px;
    }
</style>


