<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <h2>Essential Links</h2>
  
    
    <p id='title'>{{msg}}</p>
    <div>
      <el-button @click="transgo('slot')"></el-button>
      <el-button @click="transgo('eleindex')">element</el-button>
      <el-button @click="transgo('newdate')">Date</el-button>
      <el-button @click="transgo('directive')"></el-button>
      <el-button @click="console">nextTick</el-button>
      <el-button @click="transgo('editor')"></el-button>
    </div>
    <div>
      <el-button @click="transgo('calc')"></el-button>
      <el-button @click="transgo('drag')"></el-button>
      <el-button @click="transgo('draggable')">vueDraggable</el-button>
      <el-button @click="transgo('brower')"></el-button>
    </div>
    <div>
      <el-button @click="transgo('vue')">Vue</el-button>
    </div>
    <div>
      <el-button @click="transgo('jsx')">JSX</el-button>
    </div>
    <p>{{filterTest | filterNum}}</p>
    <p>{{filterTest1 | filterNum}}</p>
    <p>{{filterTest2 | filterNum}}</p>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      filterTest:1,
      filterTest1:3,
      filterTest2:-1,
    }
  },
  // filter
  filters:{
    filterNum(val){
      if(val > 1){
        return val + "1"
      }else{
        return val + "1"
      }
    }
  },
  methods:{
    transgo(val){
      this.$router.push({"name":val})
    },
    // $nextTick
    // datadom
    console(){
      this.msg = 'hello'
      var box = document.getElementById("title")
      console.log('',this.msg)
      console.log('',box.innerHTML)
      this.$nextTick(() => {
        console.log('',this.msg)
        console.log('',box.innerHTML)
      })
      
      this.msg = 'world'
      console.log('',this.msg)
      this.trans()
    },
    trans(){
      this.msg = 'change'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
div {
  margin: 20px auto;
}
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



