<template>
  <div id="wangeditor">
    <div ref="editorElem" style="text-align:left;"></div>
  </div>
</template>
<script>
import E from "wangeditor";
export default {
  name: "Editor",
  data() {
    return {
      editor: null,
      editorContent: ''
    };
  },
  watch:{
      editorContent(val){
          console.log(val)
      }
  },
  // catchDatahtml
//   props: ['catchData'], // 
  mounted() {
    this.editor = new E(this.$refs.editorElem);
    // html
    this.editor.customConfig.onchange = html => {
      this.editorContent = html;
    //   this.catchData(this.editorContent); // htmlcatchData
    };
    this.editor.customConfig.menus = [
      // 
      'head', // 
      'bold', // 
      'fontSize', // 
      'fontName', // 
      'italic', // 
      'underline', // 
      'strikeThrough', // 
      'foreColor', // 
      'backColor', // 
      'link', // 
      'list', // 
      'justify', // 
      'quote', // 
      'emoticon', // 
      'image', // 
      'table', // 
      'code', // 
      'undo', // 
      'redo' // 
    ];
    this.editor.create(); // 

  }
}
</script>


