<template>
    <div>
        <p ref='showdate' v-color='color'><span></span><span>{{today}}</span></p>
        <p><span></span><span>{{week}}</span></p>
        <input type="text" v-model="color">
    </div>
</template>
<script>
var timer,i = 0;

function start(){

    timer = setInterval(function(){i++
        console.log(i++)
    },20)
    // clearInterval(timer)
    // console.log('',i)
}
// start()
export default {
    data(){
        return {
            today:'',
            week:'',
            color:'red'
        }
    },
    mounted(){
        this.getToday()
    },
    methods:{
        getToday(){

            /* date
                
            **/ 
            // 
            var date = new Date()
            // var date = new Date(2020, 1,1)
            let year = date.getFullYear()
            let month = date.getMonth() + 1
            let day = date.getDate()
            let week = date.getDay()
            // console.log(week)
            switch(week){
                case 1:
                    console.log('')
                    this.week = ''
                    break;
                case 2:
                    console.log('')
                    this.week = ''
                    break;
                case 3:
                    console.log('')
                    this.week = ''
                    break;
                case 4:
                    console.log('')
                    this.week = ''
                    break;
                case 5:
                    console.log('')
                    this.week = ''
                    break;
                case 6:
                    console.log('')
                    this.week = ''
                    break;
                case 7:
                    console.log('')
                    this.week = ''
                    break;
                default:
                    console.log('')
                    break;
               
            }
            /* dateriqi**/ 
            // 
            var date1 = new Date(2019,11,22)
            console.log(date)
            console.log(date1)
            this.today =  year + '-' + month + '-' + day
            console.log(this.today)
        }
    }
}
</script>


