<template>
  <div>
    <span v-for="val in number" :key="val">{{val}}</span>
    <button @click="push">push8</button>
    <button @click="change">1A()</button>
    <button @click="changeLen">2()</button>
    <button @click="set">1A(set)</button>
    <button @click="spliceLen">2(splice)</button>
  </div>
</template>

<script>
export default {
  data () {
    return {
      number: [1,2,3,4,5,6,7]
    }
  },
  methods:{
    push() {
      this.number.push(8);
    },
    change() {
      this.number[0] = "A";
    },
    changeLen() {
      this.number.length = 2;
    },
    set() {
      this.$set(this.number, 0 , "A"); //VUEVue.set(this.number, 0 , "A")
    },
    spliceLen() {
      this.number.splice(2);
    }
  }
}
</script>


