<template>
    <div>
        <p></p>
        <p>11111111111111111111111111</p>
        <p>2222222222222222222222222222</p>
        <p>3333333333333333333333</p>
        <p>444444444444444444444444444444</p>
        <p>555555555555555555555555555555555555555</p>
        <el-button type="success" size="mini" plain></el-button>
        <el-button type="success" size="mini" plain @click="selectAll"></el-button>
        <el-button type="success" size="mini" plain @click="copy"></el-button>
        <el-button type="success" size="mini" plain @click="saveAs" disabled></el-button>
        <el-button type="success" size="mini" plain @click="print"></el-button>
        <el-button type="success" size="mini" plain @click="open" disabled></el-button>
        <el-button type="success" size="mini" plain @click="close"></el-button>
        <el-button type="success" size="mini" plain @click="reload"></el-button>
    </div>
</template>
<script>
export default {
    data(){
        return {

        }
    },
    methods:{
        selectAll(){
            
            document.execCommand("selectAll")
        },
        copy(){
            document.execCommand("copy")
        },
        open(){
            document.execCommand("Open")
        },
        saveAs(){
            document.execCommand('SaveAs')
        },
        print(){
            // 1
            // document.execCommand('print')

            // 2
            window.print()
        },
        reload(){
            // 1
            // window.location.reload()

            // 2
            document.location.reload()  //onload
        }
    }
}
</script>


